* {
  box-sizing: border-box;
}

body {
  margin: 16px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.button-search {
  padding: 8px 12px;
  cursor: pointer;
  width: 180px;
  height: 40px;
  border-radius: 10px;
  margin-left: 20px;
  border-color: rgb(59, 67, 77);
}


input {
  padding: 8px;
  font: inherit;
  width: 300px;
  height: 40px;
}

.search-form {
  position: fixed;
  width: 100vw;
  left: 0;
  top: 0;
  right: 0;
  background-color: rgb(72, 82, 94);
  border: 1px solid rgb(59, 67, 77);
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
  0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  text-align: center;
  height: auto;
}

:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

.gallery {
  margin-left: -10px;
  padding-top: 60px;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
}

.gallery-item {
  margin-top: 10px;
  margin-left: 10px;
  min-width: 320px;
  border: 2px solid #f1eeee;
  border-radius: 4px;
  overflow: hidden;
  transition: all .3s;
  flex-basis: calc((100% - 10px) / 5);
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
  0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.photo-card {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.gallery-image {
  display: block;
  height: 30vh;
  object-fit: cover;
}

.info {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  justify-content: space-around;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #000;
}

.gallery-item {
  text-decoration: none;
}

.load-more.is-hidden {
  display: none;
}

.load-more {
  padding: 8px 12px;
  cursor: pointer;
  width: 180px;
  height: 40px;
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  margin-top: 40px;
  border-radius: 10px;
  border-color: #fafafa;
}