* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.button-search {
  cursor: pointer;
  border-color: #3b434d;
  border-radius: 10px;
  width: 180px;
  height: 40px;
  margin-left: 20px;
  padding: 8px 12px;
}

input {
  font: inherit;
  width: 300px;
  height: 40px;
  padding: 8px;
}

.search-form {
  text-align: center;
  background-color: #48525e;
  border: 1px solid #3b434d;
  width: 100vw;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: .25s;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin-left: -10px;
  padding-top: 60px;
  display: flex;
}

.gallery-item {
  border: 2px solid #f1eeee;
  border-radius: 4px;
  flex-basis: calc(20% - 2px);
  min-width: 320px;
  margin-top: 10px;
  margin-left: 10px;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.photo-card {
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.gallery-image {
  object-fit: cover;
  height: 30vh;
  display: block;
}

.info {
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.gallery-item {
  text-decoration: none;
}

.load-more.is-hidden {
  display: none;
}

.load-more {
  cursor: pointer;
  border-color: #fafafa;
  border-radius: 10px;
  width: 180px;
  height: 40px;
  margin-top: 40px;
  padding: 8px 12px;
  position: relative;
  left: 50%;
  transform: translate(-50%);
}
